import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Info } from '../entity/info';
import { Location } from '@angular/common';
import { InfoService } from '../info.service';


@Component({
  selector: 'app-infodetail',
  templateUrl: './infodetail.component.html',
  styleUrls: ['./infodetail.component.css']
})
export class InfoDetailComponent implements OnInit {

  d = [];
  fnoption: any;
  state: number =0;
  now = new Date(1997, 9, 3);
  oneDay = 24 * 3600 * 1000;
  option: any;
  value = Math.random() * 1000;
  @Input() info: Info ;
  constructor(
  private route: ActivatedRoute,
  private infoService: InfoService,
  private location: Location
) {}
  randomData() {
    this.now = new Date(+this.now + this.oneDay);
    this.value = this.value + Math.random() * 21 - 10;
    return {
        name: this.now.toString(),
        value: [
            [this.now.getFullYear(), this.now.getMonth() + 1, this.now.getDate()].join('/'),
            Math.round(this.value)
        ]
    };
}

  ngOnInit() {
      this.graph();
      this.getinfo();
  }
    goBack(): void {
    this.location.back();
  }
    getinfo(): void {
    const id = this.route.snapshot.paramMap.get('id');
    this.infoService.getInfo(id)
      .subscribe(info => this.info = info);
  }
    save(): void {
    this.infoService.updateInfo(this.info)
      .subscribe(() => this.goBack());
  }

  graph() {
      
    for (let i = 0; i < 1000; i++) {
        this.d.push(this.randomData());
}
    this.option = {
    title: {
        text: ''
    },
    tooltip: {
        trigger: 'axis',
        formatter: (params) => {
            params = params[0];
            const date = new Date(params.name);
            return date.getDate() + '/' + (date.getMonth() + 1) + '/' + date.getFullYear() + ' : ' + params.value[1];
        },
        axisPointer: {
            animation: false
        }
    },
    xAxis: {
        type: 'time',
        splitLine: {
            show: false
        }
    },
    yAxis: {
        type: 'value',
        boundaryGap: [0, '100%'],
        splitLine: {
            show: false
        }
    },
    series: [{
        name: '',
        type: 'line',
        showSymbol: false,
        hoverAnimation: false,
        data: this.d
    }]
};

    setInterval(() => {

    for (let i = 0; i < 5; i++) {
        this.d.shift();
        this.d.push(this.randomData());
    }
    this.fnoption = {
        title: {
            text: '',
        },
        tooltip: {
            trigger: 'item',
            formatter: '{a} <br/>{b} : {c}'
        },
        legend: {
            data: ['', '' , '' , '' , '']
        },
        calculable: true,
        series: [
            {
                name: '',
                type: 'funnel',
                left: '10%',
                top: 60,
                bottom: 60,
                width: '80%',
                min: 0,
                max: 100,
                minSize: '0%',
                maxSize: '100%',
                sort: 'descending',
                gap: 2,
                label: {
                    show: true,
                    position: 'inside'
                },
                labelLine: {
                    length: 10,
                    lineStyle: {
                        width: 1,
                        type: 'solid'
                    }
                },
                itemStyle: {
                    borderColor: '#fff',
                    borderWidth: 1
                },
                emphasis: {
                    label: {
                        fontSize: 20
                    }
                },
                data: [
                    {value: 1, name: ''},
                    {value: 2, name: ''},
                    {value: 10, name: ''},
                    {value: 100, name: ''},
                    {value: 1000, name: ''}
                ]
            }
        ]
    };
    this.option = {
      title: {
          text: ''
      },
      tooltip: {
          trigger: 'axis',
          formatter: (params) => {
              params = params[0];
              const date = new Date(params.name);
              return date.getDate() + '/' + (date.getMonth() + 1) + '/' + date.getFullYear() + ' : ' + params.value[1];
          },
          axisPointer: {
              animation: false
          }
      },
      xAxis: {
          type: 'time',
          splitLine: {
              show: false
          }
      },
      yAxis: {
          type: 'value',
          boundaryGap: [0, '100%'],
          splitLine: {
              show: false
          }
      },
      series: [{
          name: '',
          type: 'line',
          showSymbol: false,
          hoverAnimation: false,
          data: this.d
      }]
  };
}, 1000);
  }
}
